@import "../public_func.sass";

span.box, a.box {
	display: inline-block;
	box-sizing: border-box;
	border: 1px solid #b3b3b3;
	background: #e9edf1;
	padding: 0 rem(4);
	line-height: rem(22);
	margin-top: rem(4); }

main {
	margin-right: auto;
	margin-left: auto;
	line-height: 1.5em;
	letter-spacing: 0.04em;
	section {
		padding-bottom: rem(50);
		width: 100%;
		float: left;
		[class ^= title] {
			text-transform: uppercase;
			font-weight: bold; }
		&>.title {
			padding: rem(120) rem(30) rem(100) rem(30);
			background: #556655;
			color: white; }
		article {
			box-sizing: border-box;
			max-width: rem(1300);
			width: 100%;
			padding: 0 rem(20);
			font-weight: 500;
			margin: 0 auto;
			&:first-child {
				padding-top: rem(20); }
			padding-top: rem(50);
			.title {
				font-size: rem(30);
				padding-bottom: rem(10); }
			a {
				color: #48a0ef;
				font-size: rem(20);
				font-weight: bold;
				text-decoration: none;
				&:hover {
					color: #0069c8; } }
			ol {
				list-style-type: decimal;
				padding-left: rem(30); }
			&>ol {
				&>li {
					padding-top: rem(20);
					&:first-child {
						padding-top: 0; }
					&>ol, &>ul {
						font-weight: 400;
						a {
							font-weight: 400; } }
					&>ol {
						list-style-type: decimal-leading-zero; }
					&>ul ul {
						list-style-type: circle; } } }
			ul {
				list-style-type: initial;
				padding-left: rem(30); }
			img {
				display: block;
				padding: rem(30) 0;
				margin: 0 auto;
				&.width-500 {
					max-width: rem(500);
					width: 100%; } } }
		article.color-box {
			div {
				line-height: rem(40);
				padding: rem(10);
				margin: rem(10);
				color: white;
				&.black {
					background: black; }
				&.blue {
					background: #48a0ef; }
				&.soldier {
					background: #556655; }
				&.red {
					background: rgba(255, 0, 29, .7); }
				&.e9edf1 {
					background: #e9edf1; } } }
		article.study-list {
			padding-bottom: rem(50);
			border-bottom: rem(3) solid black; } } }

